.notify-dropdown-contain {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 56.3px;
  margin-right: 70px;
  .bell {
    display: flex;
    flex-direction: row;
    width: fit-content;
    height: 43px;
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    #counter-notify {
      font-family: "Yusei Magic", sans-serif;
      height: 25px;
      line-height: 25px;
      border-radius: 50%;
      font-size: 15px;
      text-align: center;
      background: rgb(199, 42, 42);
      width: 26px;
      display: inline-block;
      color: black;
      margin-left: -20px;
    }
  }
  .show {
    position: absolute;
    top: 72px;
    display: flex;
    flex-direction: column;
    border: 3px solid black;
    // box-shadow: 0px 3px 9px 1px rgb(177, 173, 173);
    width: 499.5px;
    border-radius: 10px;
    background-color: hsl(27, 100%, 98%);
    margin-right: 390px;
    #notification {
      font-family: "Roboto", sans-serif;
      font-weight: bold;
      font-size: 17px;
      font-style: normal;
      padding-left: 15px;
      padding-top: 15px;
      padding-bottom: 15px;
    }
    .notify-menu {
      height: 288px;
      width: 500px;
      overflow: hidden;
    }
    a {
      text-decoration: none;
    }
    .notify-menu:hover {
      overflow-y: scroll;
    }
  }
  .hide {
    display: none;
  }
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: grey;
    background-color: hsl(27, 100%, 98%);
    border-radius: 15px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background-color: rgb(113, 175, 63);
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px green;
  }
}
